<template>
  <div
    class="box"
    :class="{ active: isActive }"
    @click="emitEvent"
    @mousedown="addClass"
  >
    <div class="box__header">
      <div class="box__header-top">
        <div class="box__header-icon">{{ data.icon }}</div>
        <div class="box__header-counter">
          {{ data.count || 0 }}
        </div>
      </div>
      <div class="box__header-bottom">
        <h3 class="box__header-headline">{{ data.headline }}</h3>
        <span class="box__header-subhead">{{ data.subhead }}</span>
      </div>
    </div>
    <div ref="scrollable" class="box__content box__scrollable">
      <ul class="box__content-list">
        <Task
          v-for="singleItem in tasks"
          :key="singleItem.id"
          :singleItem="singleItem"
          @saveTask="emitSaveTask"
          @removeTask="emitRemoveTask"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import Task from "./components/Task.vue";

export default {
  name: "Box",
  props: ["data", "tasks"],
  components: {
    Task,
  },
  data() {
    return {
      isActive: false,
    };
  },
  watch: {
    tasks() {
      this.$refs.scrollable.scrollTop = 0;
    },
  },
  methods: {
    emitSaveTask(data) {
      this.$emit("onSaveTask", data);
    },
    emitRemoveTask(data) {
      this.$emit("onRemoveTask", data);
    },
    emitEvent() {
      this.$emit("event", this.data.event);
    },

    addClass() {
      if (this.data.event !== "add-class") return;
      this.isActive = !this.isActive;
    },
  },
};
</script>

<style scoped lang="scss">
@import "./scss/Box.scss";
</style>
