<template>
  <li class="box__content-item" @mousedown.stop="stopTheEvent">
    <div>
      <input
        type="text"
        class="box__content-title"
        @keyup.enter="saveTask"
        v-model="value"
      />
      <span
        class="box__content-additional"
        v-for="(info, index) in singleItem.additional"
        :key="index"
      >
        {{ index == 0 ? info + " • " : info }}
      </span>
    </div>
    <div class="box__content-manage">
      <div class="box__content-save" @click="saveTask"><span></span></div>
      <div class="box__content-remove" @click="removeTask"><span></span></div>
    </div>
  </li>
</template>

<script>
export default {
  name: "Task",
  props: ["singleItem"],
  data() {
    return { value: this.singleItem.title };
  },
  watch: {},
  methods: {
    saveTask() {
      if (this.singleItem.title !== this.value) {
        this.$emit("saveTask", { value: this.value, id: this.singleItem.id });
      }
    },
    removeTask() {
      this.$emit("removeTask", this.singleItem.id);
    },
    stopTheEvent() {
      console.log("s");
    },
  },
};
</script>

<style scoped lang="scss">
@import "./scss/Task.scss";
</style>
